body{
    background-color: #0d778c;
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    color: white;
    font-weight: 500;
    font-size: 18px;
}
.cal{
    margin: 100px;
    margin-inline: auto;
    display: grid;
    gap: 6px;
    grid-template-columns: repeat(4, 80px);
    grid-template-rows: 110px;
    grid-auto-rows: 80px;
    background-color: rgba(0, 0, 0, 0.95);
    width: fit-content;
    border-radius: 10px;
    padding: 15px;
}
.c-btn{
    background-color: rgba(25, 27, 27, 0.867);
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    border: none;
    border-radius: 999px;
}
.output{
    grid-column: 1 / -1;
    background-color: rgba(70, 68, 68, 0.518);
    margin-bottom: 10px;
    border-radius: 5px;
}
.oprations{
    height: 40%;
    padding: 10px;
    text-align: end;
    font-size: 16px;
    background-color: rgba(59, 59, 59, 0.518);
}
.input{
    height: 60%;
    padding: 10px;
    text-align: end;
    font-size: 32px;
    font-weight: 700;
}


.ac{
    grid-column: span 2;
    border-radius: 40px;
}
.equal{
    grid-column: span 2;
    border-radius: 40px;
}

.c-btn:hover{
    background-color: rgba(255, 255, 255, 0.167);
}